import React, { lazy, Suspense } from "react";
import { createBrowserRouter, Outlet, RouterProvider } from "react-router-dom";
import { Footer, Navbar } from "../../components";
// import BookmarkPage from "../Bookmark/BookmarkPage";
// import Home from "../Home/Home";
// import NewsDetails from "../NewsDetails/NewsDetails";
// import NewsSearch from "../Search/NewsSearch";
import { Loader } from "../../components";
import styles from "./App.module.css";

const BookmarkPage = lazy(() => import("../Bookmark/BookmarkPage"));
const Home = lazy(() => import("../Home/Home"));
const NewsDetails = lazy(() => import("../NewsDetails/NewsDetails"));
const NewsSearch = lazy(() => import("../Search/NewsSearch"));

const Layout = () => {
  return (
    <div className={styles.app}>
      <Navbar />
      <Outlet />
      <Footer />
    </div>
  );
};

const router = createBrowserRouter([
  {
    path: "/",
    element: <Layout />,
    children: [
      {
        path: "/",
        element: <Home />,
      },
      {
        path: "/bookmarks/",
        element: <BookmarkPage />,
      },
      {
        path: "/news-detail/*",
        element: <NewsDetails />,
      },
      {
        path: "/news-search/:q",
        element: <NewsSearch />,
      },
    ],
  },
]);
const App = () => {
  return (
    <div>
      <Suspense fallback={<Loader />}>
        <RouterProvider router={router} />
      </Suspense>
    </div>
  );
};

export default App;
